header {
  color: #fff;
  background-color: red;
  padding: .5rem 1rem;
  font-weight: 500;
}

#app {
  height: 100vh;
}

.gcode-sender {
  flex-flow: column;
  align-items: start;
  gap: .5rem;
  height: 100%;
  padding: .5rem;
  display: flex;
}

.gcode-sender .__logs {
  background: #d6d6d6;
  flex-direction: column-reverse;
  align-self: stretch;
  min-height: 16rem;
  max-height: 50vh;
  margin: auto -.5rem -.5rem;
  padding: .5rem;
  display: flex;
  overflow: auto;
}

.gcode-sender .__main {
  grid-template-columns: repeat(2, 1fr);
  gap: .5rem;
  width: 100%;
  display: grid;
}

.gcode-sender .__gcode {
  width: 100%;
  min-height: 10rem;
}

.jog-pad {
  grid-template-columns: repeat(3, 1fr);
  gap: .25rem;
  display: grid;
}

.jog-pad > button {
  width: 3rem;
  height: 3rem;
}

.gcode-viewer {
  border: 1px solid #000;
  min-width: 800px;
  min-height: 400px;
  padding: 1rem;
  overflow: scroll;
}

main {
  grid-template-rows: auto 1fr;
  height: 100%;
  display: grid;
}

